<template>
<transition name="slide-fade">
	<div>
		<img class="cc-bg" ref="img" src="../assets/img/telbg.png" alt="">
		<div class="cc-tel flexstart">
			<a href="tel:4001810288" class="cc-l flexstart">
				<img ref="img" src="../assets/img/tel1.png" alt="">
				<span>拨打客服电话</span>
			</a>
			<a href="https://www.teambook.cc/ShangQiao.html" class="flexstart">
				<img ref="img" src="../assets/img/tel2.png" alt="">
				<span>发起在线对话</span>
			</a>
		</div>
		<div class="cc-list">
			<div class="cc-list1" v-for="(item,index) in list" :key="index">
				<div >{{index+1}}.{{item.problem}}</div>
				<p>{{item.answer}}</p>
			</div>
		</div>
	</div>
</transition>
</template>
<script type="text/javascript">
import {Toast} from 'vant';
export default {
	name: 'tel',
	components: {
		[Toast.name]: Toast,
	},
	data() {
		return {
			list:[]
		};
	},
	mounted() {
		this.jobCat();
	},
	methods: {
		jobCat(){
			var that=this;
			this.$http.get('/main/commonProblem').then(function (res) {
				if (res.code==10000) {
					that.list=res.data;
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
	},
}
</script>
<style lang="scss" scoped>
.cc-bg{
	width: 375px;
	height: 116px;
}
.cc-tel{
	position: fixed;
	left: 10px;
	bottom:10px;
	width:355px;
	height:64px;
	background:rgba(254,254,254,1);
	box-shadow:0px 2px 8px 0px rgba(40,52,51,0.2);
	border-radius:4px;
	padding:21px 0;
	a{
		display: block;
		width: 177px;
		padding-left: 28px;
		&.cc-l{
			border-right: 1px solid #eee;
		}
		img{
			width: 18px;
			height: 18px;
			margin:2px 8px  0 0;
		}
		span{
			font-size:16px;
			color:rgba(19,37,55,1);
			line-height:22px;
		}
	}
}
.cc-list{
	padding-bottom: 80px;
	.cc-list1{
		padding:20px 24px;
		background:#fff;
		margin-bottom: 5px;
		div{
			font-size:16px;
			font-weight:500;
			color:rgba(19,37,55,1);
			line-height:22px;
			margin-bottom: 8px;
		}
		p{
			color:rgba(123,130,154,1);
			line-height:20px;
		}
	}
}
</style>